<template>
  <div id="container">
    <HeaderUserBar />

    <div id="partners">
      <div class="partners-title">Партнеры</div>
      <div class="partners-grid">
<!--        <div class="partners-card">-->
<!--          <img src="../files/depomall.webp" alt="Partner #1">-->
<!--        </div>-->
        <div class="desc" v-if="user.lang === 'ru'">Спонсоры: ряд партнёров и спонсоров запустится 1 декабря 2022г.</div>
        <div class="desc" v-else>Homiylar: Bir qator hamkorlar va homiylar 2022-yil 1-dekabrda ishga tushadi.</div>
      </div>
    </div>

    <BottomNavigationMenu active="partners" />
  </div>
</template>

<script>
import HeaderUserBar from "@/components/HeaderUserBar";
import BottomNavigationMenu from "@/components/BottomNavigationMenu";
import axios from "axios";
export default {
  name: "Partners",
  components: {
    HeaderUserBar,
    BottomNavigationMenu
  },
  data: () => ({
    user_id: 0,
    user: null,
  }),
  async mounted() {
    const telegram = window.Telegram.WebApp;
    const userData = telegram.initDataUnsafe.user;

    // this.user_id = 5296876609
    this.user_id = userData.id

    await axios.get('https://server.gold-ticket.uz/api/user/' + this.user_id)
        .then(response => {
          this.user = response.data.data
        })
        .catch(error => console.log(error));

    if (!this.user.agreed) {
      this.$router.push('/terms')
    }
  }
}
</script>

